.company{
    background: linear-gradient(280deg, rgb(5, 36, 82) 24%, rgb(3, 85, 137) 95%); 
}

.company__intro{
  padding: 70px 350px 0;
  height: 100vh;
  box-sizing: border-box;
  background-size: contain;
  background-position: right;
}

@media screen and (max-width: 1920px) {
    .company__intro {
        padding: 70px 150px 0;
    }
  }

  @media screen and (max-width: 1200px) {
      .company__intro {
          padding: 70px 100px 0;
      }
    }

    
@media screen and (max-width: 1000px) {
    .company__intro {
        padding: 70px 60px 0;
        background-size: 140%;
        background-position: right bottom;
    }
  }

  
@media screen and (max-width: 650px) {
    .company__intro {
        padding: 70px 18px 0;
        height: 94vh;
        background-position: right bottom 30px;
        background-size: 160%;
    }
  }


.company__title {
    font-size: 130px;    
    font-weight: 700;    
    padding-top: 220px;
    max-width: 1200px;  
    padding-bottom: 50px;  
}

@media screen and (max-width: 1600px) {
    .company__title {
        font-size: 80px;    
        font-weight: 700;    
        padding-top: 200px;
        max-width: 1200px;  
        padding-bottom: 30px;  
    }
  }

  @media screen and (max-width: 650px) {
    .company__title {
        font-size: 56px;  
          
        padding-top: 150px;
       
        padding-bottom: 20px;  
    }
  }





.company__subtitle{
    font-size: 40px;
    line-height: 1.2;    
    max-width: 580px;    
}

@media screen and (max-width: 1600px) {
    .company__subtitle {
        font-size: 32px;       
        max-width: 460px;  
        padding-bottom: 30px;  
    }
  }

  @media screen and (max-width: 650px) {
    .company__subtitle {
        font-size: 24px;       
        max-width: 346px;  
        padding-bottom: 30px;  
    }
  }


.company__slcrolldown{
    position: absolute;
    left: 50%;
    bottom: -130px;
    z-index: 4;
}


